import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';

/**
 * Generated class for the MailPwloginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mail-pwlogin',
  templateUrl: 'mail-pwlogin.html',
})
export class MailPwloginPage {

  email: string;
  password: string;
  resetBtn:boolean;

  newpw:string;
  comfirmPw:string;

  constructor(public navCtrl: NavController,
        private auth: AuthServiceProvider,          
    public navParams: NavParams) {

      this.resetBtn = false;
      this.newpw = "";
      this.comfirmPw = "";
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MailPwloginPage');
  }

  authClick(){
    this.googleLogin();
  }

  googleLogin(){
    this.auth.google_singin();
  }

  signup() {
    this.auth.signup(this.email, this.password);
    this.email = this.password = '';
  }

  login() {
    this.auth.login(this.email, this.password);
    this.email = this.password = '';    
    
  }

  logout() {
    this.auth.logout();
  }

  resetPw() {
    this.resetBtn = true;
  }

  pwChange() {
   
    if( this.newpw == this.comfirmPw){
       
        this.auth.resetpw(this.newpw,() =>{
          console.log("pw reset ok");
          this.resetBtn = false;
        });
    }
    else{
      console.log('pw not equal ');
    }
  }

  emailPwreset(){
    this.auth.resetpwbyMail(()=>{
      console.log("mail is send");
    })
  }
}



