import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Observable } from 'rxjs';
import { Events } from 'ionic-angular';
import firebase from 'firebase/app';

import 'firebase/auth'
import 'firebase/database'

/*
  Generated class for the AuthServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthServiceProvider {

  private user: Observable<firebase.User>;
  firebase: any;

  constructor(private firebaseAuth: AngularFireAuth,
              public eventCtrl: Events,
              ) {
    console.log('Hello AuthServiceProvider Provider');
    this.user = this.firebaseAuth.authState;
  }

  public Authinit(){
   
    this.eventCtrl.subscribe('Auth:login', () => {
      console.log('user login!');
    });
    
    this.eventCtrl.subscribe('Auth:notlogin', () => {
        console.log('no user');
      });
      

    this.firebaseAuth.auth.onAuthStateChanged((user) => {
      if (user) {
        // User is signed in.
        this.eventCtrl.publish('Auth:login');
      } else {
        // No user is signed in.
        this.eventCtrl.publish('Auth:notlogin');
      }
    });
  }

  signup(email: string, password: string) {
    this.firebaseAuth
      .auth
      .createUserWithEmailAndPassword(email, password)
      .then(value => {
        console.log('Success!', value.user.getIdToken());
      })
      .catch(err => {
        console.log('Something went wrong:',err.message);
      });    
  }

  login(email: string, password: string) {
    this.firebaseAuth
      .auth
      .signInWithEmailAndPassword(email, password)
      .then(value => {
        console.log('Nice, it worked!',value.user.uid);
        
      })
      .catch(err => {
        console.log('Something went wrong:',err.message);
      });
  }


  public getuid(){
    return this.firebaseAuth.auth.currentUser.uid;
  }

  public getlanguage(){
    this.firebaseAuth.auth.useDeviceLanguage();
    return this.firebaseAuth.auth.languageCode;
  }

  resetpw(newpwd:string,f:Function){
    //this.firebaseAuth.auth.currentUser.email
  //   name = user.displayName;
  // email = user.email;
  // photoUrl = user.photoURL;
  // emailVerified = user.emailVerified;
  // uid = user.uid;  // The user's ID, unique to the Firebase project. Do NOT use
  //                  // this value to authenticate with your backend server, if
  //                  // you have one. Use User.getToken() instead.

  var user = this.firebaseAuth.auth.currentUser;
  
    user.updatePassword(newpwd).then(() =>{
      // Update successful.
      f();
    }).catch(function(error) {
      // An error happened.
      console.log("pw reset fial"+ error);
    });
  }

  resetpwbyMail(f:Function){
    this.firebaseAuth.auth.sendPasswordResetEmail(this.firebaseAuth.auth.currentUser.email).then(function() {
      // Email sent.
      f();
    }).catch(function(error) {
      // An error happened.
      console.log("emailpw reset fial"+ error);
    });
  }

  logout() {
    this.firebaseAuth
      .auth
      .signOut();
  }

  //----google sign
  google_singin(){
    let provider = new firebase.auth.GoogleAuthProvider();
    this.getlanguage();
    this.firebaseAuth.auth.signInWithPopup(provider).then((result)=>{
      let token = result.credential.providerId;
      let user = result.user;
      console.log(token);
    }).catch((error)=>{
      console.log(error);
    })
  }

  google_singout(){
    this.logout();
  }
}


