import { Component } from '@angular/core';
import { ActionSheetController } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { HintProvider } from '../../providers/hint/hint';


/**
 * Generated class for the AboutAuthComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'about-auth',
  templateUrl: 'about-auth.html'
})
export class AboutAuthComponent {

 

  constructor(
    private actionSheetCtrl:ActionSheetController,
    private hint:HintProvider,
    private socialSharing: SocialSharing
            ) {
    console.log('Hello AboutAuthComponent Component');
  }

  authClick(){
    this.presentActionSheet();
  }

  presentActionSheet() {
    const actionSheet = this.actionSheetCtrl.create({
      title: '關於',
      buttons: [
        {
          text: '作者:dyson hung',
          role: 'destructive',
          handler: () => {
            //console.log('Destructive clicked');
          }
        },{
          text: 'Email:hhg4092@gmail.com(點擊提出建議)',
          handler: () => {
            this.socialSharing.canShareViaEmail().then(() => {
              // Sharing via email is possible
                this.socialSharing.shareViaEmail('hi:', '神仙指路玩家意見', ['hhg4092@gmail.com']).then(() => {
                  // Success!
                 this.hint.msg("送出成功");
                }).catch(() => {
                  // Error!
                  this.hint.msg("送出錯誤");
                });
            }).catch(() => {
              // Sharing via email is not possible
                this.hint.msg("目前無法使用信件");
            });
            
          }
        },{
          text: '問題回報',
          role: 'destructive',
          handler: () => {
            this.socialSharing.canShareViaEmail().then(() => {
              // Sharing via email is possible
                this.socialSharing.shareViaEmail('hi 使用過程有問題...', '神仙指路bug', ['hhg4092@gmail.com']).then(() => {
                  // Success!
                 this.hint.msg("送出成功");
                }).catch(() => {
                  // Error!
                  this.hint.msg("送出錯誤");
                });
            }).catch(() => {
              // Sharing via email is not possible
                this.hint.msg("目前無法使用信件");
            });
          }
        },{
          text: '取消',
          role: 'destructive',
          handler: () => {
            
          }
        }
      ]
    });
    actionSheet.present();
  }

}



