import { ComponentsModule } from './../components/components.module';
import { SocialSharing } from '@ionic-native/social-sharing/ngx';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';


import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { HintProvider } from '../providers/hint/hint';
import { MailPwloginPage } from '../pages/mail-pwlogin/mail-pwlogin';

export const config = {
  apiKey: "AIzaSyAHhB3JTG4yjBaLzAxleSfaVnSUdN89GBk",
  authDomain: "godpoint-4ee0d.firebaseapp.com",
  databaseURL: "https://godpoint-4ee0d.firebaseio.com",
  projectId: "godpoint-4ee0d"
};

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MailPwloginPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(config),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    ComponentsModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MailPwloginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    InAppBrowser,

    AngularFireModule,
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    AuthServiceProvider,
    SocialSharing,
    HintProvider
  ]
})
export class AppModule {}



