import { MailPwloginPage } from './../mail-pwlogin/mail-pwlogin';
import { AuthServiceProvider } from './../../providers/auth-service/auth-service';
import { Component } from '@angular/core';
import {  Events, ModalController } from 'ionic-angular';
import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser/ngx';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs';
import { HintProvider } from '../../providers/hint/hint';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  private config:Observable<any[]>;

  items = [
    {'id':0,'title':'鑽石水果王','pic':'icon1'},
    {'id':1,'title':'棋聖','pic':'icon2'},
    {'id':2,'title':'金玉滿堂','pic':'icon3'},
    {'id':3,'title':'金雞報喜','pic':'icon4'},
    {'id':4,'title':'鐘馗','pic':'icon5'},
    {'id':5,'title':'五福臨門','pic':'icon6'},
    {'id':6,'title':'櫻花妹子','pic':'icon7'},
    {'id':7,'title':'跳起來','pic':'icon8'},
    {'id':8,'title':'水世界','pic':'icon9'},
    {'id':9,'title':'888','pic':'icon10'}   
  ];

  public idopen:Array<any>;

  constructor(
              private modalCtrl: ModalController,
              private _db: AngularFireDatabase,
              private auth: AuthServiceProvider,
              private hint:HintProvider,
              public eventCtrl: Events,
              private iab: InAppBrowser) {

                
                this.config = this._db.list("gameconfig").valueChanges();
  }

  ionViewDidEnter() {
    this.config.subscribe( valueOfItems => {
      this.idopen = valueOfItems;
      console.log(this.idopen);
    });
  }

  itemSelected(id: string) {
    //console.log("Selected Item", id);
    let open = this.idopen[id['id']];
    switch( open ){
      case 0:
        this.hint.msg("遊戲尚未開放");
        return;
      case 2:
        this.hint.msg("即將開放");
        return;
      case 3:
        this.hint.msg("維護中");
        return;
    }
    
    let gamefolder = id['id'] + 1;
    let option:InAppBrowserOptions ={
      location:'no',
      hardwareback:'no'
    }
    this.iab.create('http://nobudgetStudio.nctu.me/'+gamefolder+"/",
                    '_system', //chrome 開
                    //'_blank', //inAppBrowser中 開,有問題
                    option);
  }

 
  authClick(){
    const modal = this.modalCtrl.create(MailPwloginPage);
    modal.present();
  }

  gamerediect(){
    let option:InAppBrowserOptions ={
      location:'no',
      hardwareback:'no'
    }
    this.iab.create('http://nobudgetStudio.nctu.me/',
                    '_self',
                    option);
  }

  ngOnInit() {
    
      this.auth.Authinit();
  }
  
}



